import Accordion from 'accordion-js';
import 'Utils/ResponsiveHelper';

export default function initAccordion() {
	document.querySelectorAll('.faq-accordion').forEach(function(elem) {
		new Accordion([elem], {
			duration: 400
		});
	});

	document.querySelectorAll('.main-nav').forEach(function(menu) {
		const items = menu.querySelectorAll('li');

		items.forEach(function(item) {
			const link = item.querySelector(':scope > a');
			const drop = item.querySelector('.dropdown');

			if (drop) {
				item.classList.add('has-drop');
				link.classList.add('drop-opener');
			}
		});

		const openers = menu.querySelectorAll('.drop-opener');
		let activeItem = null;

		const accordionsInstance = new Accordion([menu], {
			duration: 400,
			elementClass: 'has-drop',
			triggerClass: 'drop-opener',
			panelClass: 'dropdown'
		});

		function clickHandler(e) {
			e.preventDefault();
			const target = e.currentTarget;
			const parent = target.closest('li');
			const currIndex = target.dataset.index;

			if (parent.classList.contains('is-active')) {
				accordionsInstance[0].close(currIndex);
				activeItem = null;
			} else {
				if (activeItem && currIndex !== activeItem) {
					accordionsInstance[0].close(activeItem);
					activeItem = null;
				}

				accordionsInstance[0].toggle(currIndex);
				activeItem = currIndex;
			}
		}

		ResponsiveHelper.addRange({
			'..1023': {
				on: () => {
					openers.forEach((link, i) => {
						link.dataset.index = i;
						link.addEventListener('click', clickHandler);
					});
				}
			},
			'1024..': {
				on: () => {
					openers.forEach((link) => {
						link.removeEventListener('click', clickHandler);
					});
				}
			}
		});
	});
}