import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default function initSliderParallax() {
	const header = jQuery('.header');

	jQuery('.history').each(function() {
		const wrapper = jQuery(this);
		const holder = wrapper.find('.history__list');
		const items = holder.find('>li');
		let headerHeight = 0;
		let resizeTimer = null;
		let totalWidth = 0;
		let isMobile = false;

		gsap.to(wrapper[0], {
			ease: 'none',
			scrollTrigger: {
				trigger: wrapper[0],
				invalidateOnRefresh: true,
				start: () => `top ${headerHeight}`,
				end: () => '+=' + (holder.outerWidth() - wrapper.outerWidth()) * 2,
				onUpdate: ({progress}) => {
					holder.css({
						marginLeft: 0 - ((holder.outerWidth() - wrapper.outerWidth()) * progress)
					});

					items.each(function(i) {
						const item = jQuery(this);
						const itemOffset = item.offset().left;
						const wrapperOffset = wrapper.offset().left;
						const wrapperWidth = wrapper.outerWidth();

						if (i > 0 && itemOffset + item.outerWidth() + 80 <= wrapperOffset + wrapperWidth) {
							item.addClass('in-viewport');
						}
					});
				},
				pin: wrapper,
				scrub: true
			}
		});

		resizeHandler();

		function resizeHandler() {
			headerHeight = header.outerHeight();

			ScrollTrigger.refresh();

			if (!isMobile) {
				totalWidth = 0;

				items.each(function() {
					totalWidth += jQuery(this).outerWidth(true);
				});

				holder.css({
					width: totalWidth
				});
			}
		}

		jQuery(window).on('resize orientationchange', function() {
			clearTimeout(resizeTimer);

			resizeTimer = setTimeout(() => {
				resizeHandler();
			}, 600);
		});
	});
}