(function($) {
	'use strict';

	function LavaMenu(options) {
		this.options = $.extend({
			holder: null,
			items: '> li',
			lineItem: '<span class="lava-line"></span>',
			animSpeed: 600
		}, options);

		this.init();
	}

	LavaMenu.prototype = {
		init: function() {
			if (this.options.holder) {
				this.findElements();
				this.attachEvents();
				this.makeCallback('onInit', this);
			}
		},
		findElements: function() {
			this.holder = $(this.options.holder);
			this.items = this.holder.find(this.options.items);
			this.back = jQuery(this.options.lineItem);
			this.holder.append(this.back);
			this.page = $('html');

			this.activeIndex = this.items.index(this.items.filter('[aria-selected="true"]'));

			if (this.activeIndex < 0) {
				this.activeIndex = 0;
			}

			this.timer = null;

			this.prepare(false);
		},
		prepare: function(flag) {
			var speed = flag ? this.options.animSpeed : 0;

			if (this.activeIndex < 0) {
				this.resetBack(speed);
			} else {
				this.activate(this.activeIndex, speed);
			}
		},
		activate: function(index, speed) {
			var item = this.items.eq(index);

			this.back.stop().animate({
				left: item.position().left + parseFloat(item.css('margin-left')),
				opacity: 1,
				width: item.outerWidth()
			}, speed);
		},
		resetBack: function(speed) {
			this.back.stop().animate({
				width: 0,
				opacity: 0,
				left: '-20%'
			}, speed);
		},
		attachEvents: function() {
			var self = this;

			// this.hoverHandler = function() {
			// 	self.activate(self.items.index(jQuery(this)));
			// };

			// this.items.on('mouseenter touchstart', this.hoverHandler);

			this.leaveHandler = function() {
				self.prepare(true);
			};

			this.items.on('mouseleave', this.leaveHandler).on('click', function() {
				setTimeout(() => {
					self.activeIndex = self.items.index(jQuery(this));
					self.timer = null;
					self.prepare(true);
				}, 100);
			}).on('keyup', function(e) {
				if (e.keyCode === 13 || e.keyCode === 32) {
					self.activeIndex = self.items.index(jQuery(this));
					self.timer = null;
					self.prepare(true);
				}
			});

			this.resizeHandler = function() {
				clearTimeout(self.timer);

				self.timer = setTimeout(function() {
					self.prepare(false);
				});
			};

			this.outsideClickHandler = function(e) {
				var target = $(e.target);

				if (!target.closest(self.holder).length) {
					self.prepare(true);
				}
			};

			jQuery(window).on('resize orientationchange', this.resizeHandler);
			this.page.on('touchstart', this.outsideClickHandler);
		},
		makeCallback: function(name) {
			if (typeof this.options[name] === 'function') {
				var args = Array.prototype.slice.call(arguments);

				args.shift();
				this.options[name].apply(this, args);
			}
		}
	};

	$.fn.lavaMenu = function(options) {
		return this.each(function() {
			$(this).data('LavaMenu', new LavaMenu($.extend(options, {holder: this})));
		});
	};
}(jQuery));