import 'Plugins/inViewportPlugin';

// in viewport init
export default function initInViewport() {
	jQuery('.viewport-block').itemInViewport({
		visibleMode: 200
	});

	document.querySelectorAll('.scroll-holder').forEach((holder) => {
		const line = holder.querySelector('.anim-lines path');
		const items = holder.querySelectorAll('.history__list > li');

		jQuery(holder).on('in-viewport', function(e, state) {
			if (state) {
				startAnimation();
			}
		}).itemInViewport({
			visibleMode: 2
		});

		function startAnimation() {
			const length = line.getTotalLength();

			line.style.strokeDasharray = length;
			line.style.strokeDashoffset = length;
			line.getBoundingClientRect();

			line.style.transition = 'stroke-dashoffset 1.5s linear';
			line.style.WebkitTransition = 'stroke-dashoffset 1.5s linear';
			line.style.strokeDashoffset = '0';

			setTimeout(() => {
				items[1].classList.add('in-viewport');
			}, 500);
		}
	});
}