import 'Plugins/filteringPlugin';

export default function initAjaxFiltering() {
	jQuery('.filter-section').ajaxFiltering({
		container: '.card-row',
		items: '.col',
		filterLists: '[data-category]',
		filterLinks: '[data-filter]',
		activeClass: 'active',
		loadClass: 'loading',
		btnMore: '.btn-more',
		paging: '.nav-links',
		usePaging: false,
		infiniteScroll: false
	});

	document.querySelectorAll('.filter-list').forEach((list) => {
		const items = list.querySelectorAll('li');

		if (items.length > 5) {
			list.classList.add('more-5');
		}
	});
}