/*
 * Ajax filtering plugin
 */
;(function($) {
	function AjaxFiltering(options) {
		this.options = $.extend({
			container: '.js-ajax-container',
			items: '.filter-item',
			filterForm: '.filter-form',
			searchForm: '.search-form',
			filterLists: '[data-category]',
			filterLinks: '[data-filter]',
			filterSelects: 'select',
			filterCheckboxes: ':checkbox',
			activeClass: 'active',
			loadingClass: 'loading',
			filteringClass: 'filtering',
			btnMore: '.btn-more',
			btnReset: '.btn-reset',
			ajaxPrefix: 'ajax=1',
			paging: '.nav-links',
			usePaging: false,
			infiniteScroll: false,
			animSpeed: 500,
			delay: 500
		}, options);

		this.init();
	}

	AjaxFiltering.prototype = {
		init: function() {
			if (this.options.holder) {
				this.findElements();
				this.attachEvents();
				this.makeCallback('onInit', this);
			}
		},
		findElements: function() {
			this.win = $(window);
			this.holder = $(this.options.holder);
			this.container = this.holder.find(this.options.container);
			this.filterLists = this.holder.find(this.options.filterLists);
			this.searchForm = this.holder.find(this.options.searchForm);
			this.filterForm = this.holder.find(this.options.filterForm);
			this.filterSelects = this.holder.find(this.options.filterSelects);
			this.btnReset = this.holder.find(this.options.btnReset);
			this.btnMore = this.holder.find(this.options.btnMore);
			this.paging = this.holder.find(this.options.paging);
			this.ajaxBusy = false;
			this.activeFilters = '';
		},
		attachEvents: function() {
			this.btnMore.on('click', (e) => {
				e.preventDefault();

				if (!this.ajaxBusy) {
					this.ajaxRequest(this.btnMore.attr('href'));
				}
			});

			this.sendRequest = () => {
				if (!this.ajaxBusy) {
					this.ajaxRequest(null);
				}
			};

			this.onScrollHandler = () => {
				this.scrollHandler();
			};

			this.attachLinksEvents();

			if (this.options.infiniteScroll) {
				this.win.on('scroll', this.onScrollHandler);
			}
		},
		attachLinksEvents: function() {
			this.filterLists.each((i, item) => {
				const list = $(item);
				const filterLinks = list.find(this.options.filterLinks);
				const allItemsParents = filterLinks.parent();

				filterLinks.on('click', (e) => {
					e.preventDefault();

					if (this.ajaxBusy) return;

					const link = $(e.target);
					const parent = link.parent();

					if (list.data('multiselect')) {
						let state = false;

						if (parent.hasClass(this.options.activeClass)) {
							parent.removeClass(this.options.activeClass);
						} else {
							parent.addClass(this.options.activeClass);
							state = true;
						}

						if (link.data('filter').trim().toLowerCase() === 'all') {
							const allParents = list.children();

							if (state) {
								allParents.addClass(this.options.activeClass);
							} else {
								allParents.removeClass(this.options.activeClass);
							}
						} else {
							filterLinks.each((i, elem) => {
								const item = $(elem);

								if (item.data('filter').trim().toLowerCase() === 'all') {
									if (!state) {
										item.parent().removeClass(this.options.activeClass);
									}

									if (list.find('.' + this.options.activeClass).length === filterLinks.length - 1) {
										item.parent().addClass(this.options.activeClass);
									}
								}
							});
						}
					} else {
						if (parent.hasClass(this.options.activeClass)) {
							parent.removeClass(this.options.activeClass);
						} else {
							allItemsParents.removeClass(this.options.activeClass);
							parent.addClass(this.options.activeClass);
						}
					}

					this.updateFilters();
					this.sendRequest();
				});
			});

			this.filterForm.on('submit', (e) => {
				e.preventDefault();
				this.sendRequest();
			});

			this.searchForm.on('submit', (e) => {
				e.preventDefault();
				this.sendRequest();
			});

			this.filterSelects.on('change', () => {
				this.sendRequest();
			});

			this.paging.on('click', 'a', (e) => {
				e.preventDefault();

				const link = $(e.target);

				if (!this.ajaxBusy) {
					this.ajaxRequest(null, link.data('page') || '1');
				}
			});

			this.btnReset.on('click', () => {
				if (this.filterForm.length) {
					this.filterForm[0].reset();
				}

				if (this.searchForm.length) {
					this.searchForm[0].reset();
				}

				this.filterSelects.each((i, item) => {
					const select = jQuery(item);
					const options = select.find('option');

					options.removeAttr('selected');
					options.eq(0).attr('selected', true);
				});

				setTimeout(() => {
					this.sendRequest();
				}, 500);
			});
		},
		scrollHandler: function() {
			if (this.win.scrollTop() + ((this.win.height() / 5) * 4) >= this.container.offset().top + this.container.outerHeight()) {
				if (!this.ajaxBusy && this.btnMore.length) {
					this.ajaxRequest(this.btnMore.attr('href'));
				}
			}
		},
		updateFilters: function() {
			this.activeFilters = '';

			this.filterLists.each((i, item) => {
				const currList = $(item);
				const activeItems = currList.find('.' + this.options.activeClass + ' > ' + this.options.filterLinks);
				let filters = [];

				activeItems.each((i, elem) => {
					filters.push($(elem).data('filter').trim());
				});

				if (filters.length) {
					for (let i = 0; i < filters.length; i++) {
						this.activeFilters += '&' + currList.data('category') + '=' + filters[i];
					}
				}
			});
		},
		ajaxRequest: function(src, pageNum) {
			const filterFormSerialize = this.filterForm.length ? '&' + this.filterForm.serialize() : '';
			const searchFormSerialize = this.searchForm.length ? '&' + this.searchForm.serialize() : '';
			let page = '';

			if (this.options.usePaging) {
				page = pageNum ? '&page_num=' + pageNum : '&page_num=1';
			}

			this.ajaxBusy = true;
			this.btnMore.hide();
			this.holder.addClass(this.options.loadingClass);
			this.makeCallback('sendRequest', this);

			if (!src) {
				this.holder.addClass(this.options.filteringClass);
			}

			$.ajax({
				url: src ? src : this.holder.data('action'),
				type: this.holder.data('method') || 'POST',
				data: this.options.ajaxPrefix + this.activeFilters + filterFormSerialize + searchFormSerialize + page,
				dataType: 'text',
				success: (data) => {
					let ifPageLoad = pageNum ? true : false;

					this.onSuccess(data, src, ifPageLoad);
				}
			});
		},
		onSuccess: function(data, src, ifPageLoad) {
			const loadedBlock = $('<div/>').html(data);
			const items = loadedBlock.find(this.options.items);
			const btnMore = loadedBlock.find(this.options.btnMore);
			const paging = loadedBlock.find(this.options.paging);

			this.makeCallback('onLoad', items);

			setTimeout(() => {
				if (!src) {
					this.container.empty();

					this.makeCallback('onFilter', this, loadedBlock);
				}

				this.holder.removeClass(this.options.filteringClass);

				items.hide().appendTo(this.container);

				if (btnMore.length) {
					this.btnMore.attr('href', btnMore.attr('href'));

					if (this.options.infiniteScroll) {
						this.win.on('scroll', this.onScrollHandler);
					} else {
						this.btnMore.show();
					}
				} else {
					if (this.options.infiniteScroll) {
						this.win.off('scroll', this.onScrollHandler);
					}
				}

				if (paging.length) {
					this.paging.html(paging.html());
				} else {
					this.paging.html('');
				}

				this.makeCallback('onSuccess', items);
				this.holder.removeClass(this.options.loadingClass);

				items.fadeIn(this.options.animSpeed);

				if (ifPageLoad) {
					$('html, body').animate({
						scrollTop: this.holder.offset().top
					}, 650);
				}

				this.ajaxBusy = false;
			}, this.options.delay);
		},
		makeCallback: function(name) {
			if (typeof this.options[name] === 'function') {
				const args = Array.prototype.slice.call(arguments);

				args.shift();
				this.options[name].apply(this, args);
			}
		}
	};

	$.fn.ajaxFiltering = function(opt) {
		return this.each((i, elem) => {
			$(elem).data('AjaxFiltering', new AjaxFiltering($.extend(opt, {
				holder: elem
			})));
		});
	};
}(jQuery));