import AriaTablist from 'aria-tablist';

// content tabs init
export default function initTabs() {
	const animateClass = 'animate';
	const hiddenClass = 'hidden';

	document.querySelectorAll('.tabset').forEach(function(elem) {
		AriaTablist(elem);
	});

	jQuery('.features, .faq').each((i, item) => {
		const holder = jQuery(item);
		const buttons = holder.find('.tabset button');
		const tabs = holder.find('[role="tabpanel"]');

		buttons.on('click', function() {
			const button = jQuery(this);
			const id = button.attr('id');
			const newTab = holder.find(`[aria-labelledby="${id}"]`);

			const currTab = tabs.filter((i, item) => {
				return !jQuery(item).attr('hidden');
			});

			currTab.add(newTab).addClass(animateClass);
			newTab.addClass(hiddenClass);

			setTimeout(() => {
				currTab.removeClass(animateClass);
				newTab.removeClass(hiddenClass);

				setTimeout(() => {
					newTab.removeClass(animateClass);
				});
			}, 350);
		});
	});
}