import hljs from 'highlight.js';
import php from 'highlight.js/lib/languages/php';

export default function initHighlightCode() {
	hljs.registerLanguage('php', php);

	document.querySelectorAll('.code-block').forEach(function(holder) {
		const block = holder.querySelector('.code-holder code');

		hljs.highlightBlock(block);

		const rowCount = block.innerHTML.split(/\n/).length - 1;
		const rowsNumList = document.createElement('ul');

		rowsNumList.className = 'rows-num-list';

		for (let i = 1; i <= rowCount; i++) {
			const row = document.createElement('li');

			row.innerHTML = i;
			rowsNumList.appendChild(row);
		}

		holder.appendChild(rowsNumList);
	});
}