import 'Utils/ResponsiveHelper'
import 'Plugins/mobileNavPlugin';

// mobile menu init
export default function initMobileNav() {
	ResponsiveHelper.addRange({
		'..1023': {
			on: function() {
				jQuery('body').mobileNav({
					menuActiveClass: 'nav-active',
					menuOpener: '.nav-opener'
				});
			},
			off: function() {
				jQuery('body').mobileNav('destroy');
			}
		}
	});

	const featuresDrops = jQuery('.features__dropdown');
	const faqDrops = jQuery('.faq-dropdown');
	const filterDrops = jQuery('.filter-dropdown');

	ResponsiveHelper.addRange({
		'..767': {
			on: function() {
				featuresDrops.mobileNav({
					menuActiveClass: 'drop-active',
					menuOpener: '.features__dropdown-opener',
					hideOnClickOutside: true,
					menuDrop: '.features__dropdown-popup'
				});

				faqDrops.mobileNav({
					menuActiveClass: 'drop-active',
					menuOpener: '.faq-dropdown__opener',
					hideOnClickOutside: true,
					menuDrop: '.faq-dropdown__popup'
				});

				filterDrops.mobileNav({
					menuActiveClass: 'drop-active',
					menuOpener: '.filter-dropdown__opener',
					hideOnClickOutside: true,
					menuDrop: '.filter-dropdown__popup'
				});
			},
			off: function() {
				featuresDrops.mobileNav('destroy');
				faqDrops.mobileNav('destroy');
				filterDrops.mobileNav('destroy');
			}
		}
	});

	featuresDrops.add(faqDrops).add(filterDrops).each((i, item) => {
		const holder = jQuery(item);
		const opener = holder.find('.features__dropdown-opener, .faq-dropdown__opener, .filter-dropdown__opener');
		const tabLinks = holder.find('.tabset button, .filter-list button');
		const activeLink = tabLinks.filter('[aria-selected="true"]');

		if (activeLink.length) {
			opener.html(activeLink.html());
		}

		tabLinks.on('click', function() {
			const link = jQuery(this);

			if (holder.data('MobileNav')) {
				holder.data('MobileNav').hide();
			}

			opener.html(link.html());
		});
	});
}