import 'Plugins/lavalampPlugin';

export default function initLavalamp() {
	const activeClass = 'active';

	// tabs lavalamp
	jQuery('.tabset:not(.faq__tabset), .filter-list, .banking__nav-slider .slick-track').lavaMenu({
		items: 'button',
		lineItem: '<span class="lavalamp-line"></span>',
		animSpeed: 300
	});

	// menu lavalamp
	document.querySelectorAll('.main-nav .dropdown__list-holder, .faq-dropdown__popup').forEach((holder) => {
		const lavalamp = holder.querySelector('.decor');
		const lists = holder.querySelectorAll('.dropdown__list, .dropdown__menu, .faq__tabset');
		const items = holder.querySelectorAll('.dropdown__list > li, .dropdown__menu > li, .faq__tabset > button');
		let activeItem = null;

		items.forEach((item) => {
			if (item.classList.contains(activeClass) || item.getAttribute('aria-selected') === 'true') {
				activeItem = item;
				setLavalampPosition(lavalamp, item);
			}

			if (!holder.classList.contains('faq-dropdown__popup')) {
				item.addEventListener('mouseover', () => {
					setLavalampPosition(lavalamp, item);
				});
			}

			item.addEventListener('click', () => {
				activeItem = item;
				setLavalampPosition(lavalamp, item);
			});
		});

		lists.forEach((list) => {
			list.addEventListener('mouseleave', (event) => {
				const isCursorInsideList = list.contains(event.relatedTarget);

				if (!isCursorInsideList) {
					if (activeItem) {
						setLavalampPosition(lavalamp, activeItem);
					} else {
						lavalamp.style.height = '100%';
						lavalamp.style.top = '0';
					}
				}
			});
		});

		function resizeHandler() {
			if (activeItem) {
				setLavalampPosition(lavalamp, activeItem);
			}
		}

		resizeHandler();

		window.addEventListener('resize', resizeHandler);
		window.addEventListener('orientationchange', resizeHandler);
	});

	function setLavalampPosition(lavalamp, item) {
		lavalamp.style.height = `${item.offsetHeight}px`;
		lavalamp.style.top = `${item.offsetTop}px`;
	}
}