import 'Plugins/stickyScrollBlockPlugin';

// initialize fixed blocks on scroll
export default function initStickyScrollBlock() {
  jQuery('.faq-dropdown').stickyScrollBlock({
    setBoxHeight: false,
    activeClass: 'fixed-position',
    container: '.faq',
    positionType: 'fixed',
    extraTop: function() {
      var totalHeight = 0;
      jQuery('.header').each(function() {
        totalHeight += jQuery(this).outerHeight() + 20;
      });
      return totalHeight;
    }
  });
}