import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default function initImageParallax() {
	document.querySelectorAll('[data-speed]').forEach((image) => {
		let startPosition = 'top bottom';
		const indent = image.dataset.speed;

		gsap.fromTo(image, {
			marginTop: 0
		}, {
			marginTop: indent,
			ease: 'none',
			scrollTrigger: {
				trigger: image,
				start: startPosition,
				end: () => 'bottom ' + -indent,
				scrub: true,
				invalidateOnRefresh: true
			}
		});
	});
}