import 'slick-carousel';
import 'Utils/ResponsiveHelper';

export default function initSlickCarousel() {
	jQuery('.hero__slider').slick({
		slidesToScroll: 1,
		rows: 0,
		arrows: false,
		dots: true,
		fade: true,
		autoplay: true,
		autoplaySpeed: 2500
	});

	jQuery('.testimonials__slider').slick({
		slidesToScroll: 1,
		rows: 0,
		arrows: false,
		dots: true,
		autoplay: true,
		autoplaySpeed: 2500
	});

	jQuery('.tabs-holder').each(function() {
		const holder = jQuery(this);
		const sliderNav = holder.find('.banking__nav-slider');
		const slider = holder.find('.banking__content-slider');

		if (!sliderNav.length || !slider.length) return;

		slider.slick({
			slidesToScroll: 1,
			rows: 0,
			arrows: false,
			infinite: false,
			pauseOnHover: false,
			asNavFor: sliderNav
		});

		sliderNav.slick({
			slidesToScroll: 1,
			rows: 0,
			arrows: false,
			infinite: false,
			pauseOnHover: false,
			variableWidth: true,
			asNavFor: slider,
			focusOnSelect: true
		});
	});

	jQuery('.api__content-slider').slick({
		slidesToScroll: 1,
		rows: 0,
		dots: true,
		fade: true,
		arrows: false,
		infinite: false,
		draggable: false,
		pauseOnHover: false,
		swipe: false,
		touchMove: false,
		adaptiveHeight: true,
		asNavFor: '.api__nav-slider',
		appendDots: '.api-holder__footer'
	});

	jQuery('.api__nav-slider').slick({
		slidesToScroll: 1,
		rows: 0,
		arrows: false,
		infinite: false,
		pauseOnHover: false,
		variableWidth: true,
		asNavFor: '.api__content-slider',
		focusOnSelect: true
	});

	document.querySelectorAll('.payments-section').forEach((holder) => {
		const mobileBlock = holder.querySelector('.mobile-view');
		const items = holder.querySelectorAll('.block-col:not(.main-block-col)');
		const mainBlock = holder.querySelector('.main-block-col');
		const mobileRows = mobileBlock.querySelectorAll('.inner-row');
		const itemsCount = items.length;
		const itemsPerRow = Math.floor(itemsCount / 2);
		let mobileMainBlock = null;

		if (itemsCount === 2) {
			holder.classList.add('two-items');
		} else if (itemsCount === 3) {
			holder.classList.add('three-items');
		} else if (itemsCount === 4) {
			holder.classList.add('four-items');
		} else if (itemsCount === 5) {
			holder.classList.add('five-items');
		} else if (itemsCount === 6) {
			holder.classList.add('six-items');
		} else if (itemsCount === 7) {
			holder.classList.add('seven-items');
		}

		ResponsiveHelper.addRange({
			'..1023': {
				on: () => {
					items.forEach((item, index) => {
						const cloneItem = item.cloneNode(true);

						if (index < itemsPerRow) {
							mobileRows[0].appendChild(cloneItem);
						} else {
							mobileRows[1].appendChild(cloneItem);
						}
					});

					mobileMainBlock = mainBlock.cloneNode(true);

					mobileBlock.insertBefore(mobileMainBlock, mobileRows[1]);

					mobileRows.forEach((row, i) => {
						const slider = jQuery(row);
						const slides = row.querySelectorAll('.block-col');
						const slidesCount = slides.length;
						const postfix = i === 0 ? 'top' : 'bottom';
						let isCenterSlides = slides.length > 2 ? true : false;

						slider.slick({
							slidesToScroll: 1,
							initialSlide: isCenterSlides ? 1 : 0,
							rows: 0,
							arrows: false,
							infinite: false,
							dots: false,
							autoplay: false
						});

						if (slides.length <= 2) {
							mobileMainBlock.classList.add('hide-left-line');
							mobileMainBlock.classList.add(`hide-left-line__${postfix}`);
						}

						if (slides.length === 1) {
							mobileMainBlock.classList.add('hide-right-line');
							mobileMainBlock.classList.add(`hide-right-line__${postfix}`);
						}

						slider.on('beforeChange', function(event, slick, currentSlide, nextSlide) {
							mobileMainBlock.classList.remove('hide-left-line');
							mobileMainBlock.classList.remove(`hide-left-line__${postfix}`);
							mobileMainBlock.classList.remove('hide-right-line');
							mobileMainBlock.classList.remove(`hide-right-line__${postfix}`);

							if (nextSlide === 0 && mobileMainBlock) {
								mobileMainBlock.classList.add('hide-left-line');
								mobileMainBlock.classList.add(`hide-left-line__${postfix}`);
							}

							if (nextSlide === slidesCount - 1) {
								mobileMainBlock.classList.add('hide-right-line');
								mobileMainBlock.classList.add(`hide-right-line__${postfix}`);
							}
						});
					});
				}
			},
			'1024..': {
				on: () => {
					mobileRows.forEach((row) => {
						if (row.classList.contains('slick-initialized')) {
							jQuery(row).slick('destroy');
							row.innerHTML = '';
						}

						if (mobileMainBlock) {
							mobileMainBlock.remove();
						}
					});
				}
			}
		});
	});
}