import ready, {HTML} from './utils';
import initAccessibilityNav from './components/accessibilityNav';
import initAccordion from './components/accordion';
import initTabs from './components/tabs';
import initMobileNav from './components/mobileNav';
import initSlickCarousel from './components/slick';
import initInViewport from './components/inViewport';
import mobileTileSection from './components/mobileTileSection';
import initLavalamp from './components/lavalamp';
import initLineAnimations from './components/lineAnimations';
import initSliderParallax from './components/sliderParallax';
import initHighlightCode from './components/highlightCode';
import initImageParallax from './components/imageParallax';
import initFiltering from './components/filtering';
import initCustomSelect from './components/customSelect';
import initVideo from './components/video';
import initStickyScrollBlock from './components/stickyScrollBlock';

ready(() => {
	HTML.classList.add('is-loaded');
	initAccessibilityNav();
	initSliderParallax();
	initMobileNav();
	initAccordion();
	initTabs();
	initSlickCarousel();
	initLavalamp();
	initHighlightCode();
	initImageParallax();
	mobileTileSection();
	initFiltering();
	initCustomSelect();
	// initStickyScrollBlock();
	initVideo();
});

jQuery(window).on('load', () => {
	initLineAnimations();
	initInViewport();
});