import '../plugins/selectPlugin'

export default function initSelect2 () {
  const activeClass = 'opened'

  document.querySelectorAll('.custom-select').forEach((select) => {
	let wrapper = document.createElement('span')

	wrapper.className = 'custom-select-wrapper'
	wrap(select, wrapper)

	let innerBox = document.createElement('span')

	innerBox.className = 'select-inner'
	wrap(select, innerBox)

    wrapper.append(innerBox)

    new Choices(select, {
      searchEnabled: false,
      itemSelectText: ''
    })

    const opener = wrapper.querySelector('.choices__list--single')

    wrapper.style.setProperty('--select-width', opener.clientWidth + 'px')

    select.addEventListener('showDropdown', () => {
      wrapper.classList.add(activeClass)

      // setTimeout(() => {
      //   innerBox.css({
      //     height: jQuery('.select2-dropdown').outerHeight() + wrapper.outerHeight()
      //   })
      // })
    }, false)

    select.addEventListener('hideDropdown', () => {
      wrapper.classList.remove(activeClass)

      wrapper.style.setProperty('--select-width', opener.clientWidth + 'px')

      // innerBox.css({
      //   height: '100%'
      // })
    }, false)
  })

  function wrap(element, wrapper) {
    element.parentNode.insertBefore(wrapper, element)
    wrapper.appendChild(element)
  }
}
