import 'Utils/ResponsiveHelper';

export default function initLineAnimations() {
	const animateClass = 'in-viewport';
	const activeClass = 'active';

	document.querySelectorAll('.info-tile').forEach((holder) => {
		const boxes = holder.querySelectorAll('.info-tile__col');
		let linesSVG = null;
		let lines = null;
		let bottomSVG = null;
		let bottomLine = null;
		let scrollTop = window.scrollY;
		let offsetsObj = {};
		let counter = 0;
		let isAnim = false;
		let delay = 1500;
		let stepsCount = 0;
		let isMobile = false;
		let svgAnimSpeed = 1.2;

		ResponsiveHelper.addRange({
			'..767': {
				on: () => {
					// for mobile
					linesSVG = holder.querySelector('.top-line-mobile');
					lines = linesSVG ? linesSVG.querySelectorAll('path, line') : [];
					bottomSVG = holder.querySelector('.bottom-line-mobile');
					bottomLine = linesSVG ? bottomSVG.querySelectorAll('path, line') : [];
					isMobile = true;
					// delay between animations
					delay = 500; // in milliseconds
					// speed of svg (top and bottom lines) animation
					svgAnimSpeed = 0.6; // in seconds
				}
			},
			'768..': {
				on: () => {
					// for desktop
					linesSVG = holder.querySelector('.top-lines-desktop');
					lines = linesSVG ? linesSVG.querySelectorAll('path, line') : [];
					isMobile = false;
					delay = 1500;
					svgAnimSpeed = 1.2;
				}
			}
		});

		function resizeHandler() {
			offsetsObj = {};

			boxes.forEach(function(box) {
				const offset = box.getBoundingClientRect().top + scrollTop;
				const propName = 'offsetProp-' + offset;

				box.dataset.offset = offset;
				box.dataset.height = box.offsetHeight;

				if (offsetsObj[propName]) {
					offsetsObj[propName] = offsetsObj[propName].concat(box);
				} else {
					offsetsObj[propName] = [box];
				}
			});

			stepsCount = Object.keys(offsetsObj).length;
		}

		function scrollHandler() {
			scrollTop = window.scrollY;

			if (Object.keys(offsetsObj).length) {
				for (let key in offsetsObj) {
					if (scrollTop + window.innerHeight >= +offsetsObj[key][0].dataset.offset + 100 && !isAnim) {
						isAnim = true;

						if (counter === 0) {
							startAnimation(linesSVG, lines);
						}

						setTimeout(() => {
							startBoxesAnimations(key);
						}, counter === 0 ? (isMobile ? delay : 0) : delay);
					}
				}
			} else {
				window.removeEventListener('scroll', scrollHandler);
				window.removeEventListener('resize', resizeHandler);
				window.removeEventListener('orientationchange', resizeHandler);
			}
		}

		function startBoxesAnimations(key) {
			if (offsetsObj[key]) {
				offsetsObj[key].forEach((box) => {
					box.classList.add(animateClass);
				});

				delete offsetsObj[key];
			}

			isAnim = false;
			counter++;

			scrollHandler();

			if (counter === stepsCount) {
				setTimeout(() => {
					delay = 0;
				}, 1000);

				setTimeout(() => {
					startAnimation(bottomSVG, bottomLine);
				}, delay);
			}
		}

		function startAnimation(svg, lines) {
			if (svg) {
				svg.classList.add(activeClass);
			}

			if (lines) {
				lines.forEach((line) => {
					const length = line.getTotalLength();

					line.style.strokeDasharray = length;
					line.style.strokeDashoffset = length;
					line.getBoundingClientRect();

					line.style.transition = `stroke-dashoffset ${svgAnimSpeed}s linear`;
					line.style.WebkitTransition = `stroke-dashoffset ${svgAnimSpeed}s linear`;
					line.style.strokeDashoffset = '0';
				});
			}
		}

		resizeHandler();

		setTimeout(() => {
			resizeHandler();
		}, 500);

		window.addEventListener('resize', resizeHandler);
		window.addEventListener('orientationchange', resizeHandler);
		window.addEventListener('scroll', scrollHandler);
	});
}